body {
  font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 1.5em;
  font-weight: 400;
  background: #f5f5f5;
}

.App {
  margin: 0 auto;
  width: 600px;
  background: #ffffff;
  border-radius: 5px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}

.PostList {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.PostList li {
  border: 1px solid #808080;
  border-radius: 5px;
  margin: 1px 0;
  padding: 15px;
}

.PostList li h1 {
  margin: 0;
  font-size: 14px;
  font-weight: 500;
}

.PostList li p {
  margin: 0;
  font-size: 12px;
}

.user {
  font-size: 10px;
  color: #8a8a8a;
}

.PostList li button {
  display: block;
  width: 100%;
  background: #e8e8e8;
  border: none;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
}

.CommentsList {
  list-style: none;
  margin: 5px 0 0;
  padding: 0;
}

.CommentsList li {
  border: 1px solid #808080;
  border-radius: 5px;
  margin: 1px 0;
  padding: 15px;
}

.CommentsList li h1 {
  font-size: 14px;
  font-weight: 500;
}

.comment-email {
  font-size: 10px;
  color: #8a8a8a;
}